body {
  font-family: "Lato", sans-serif;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

h1 {
  font-size: 3.5rem;
  color: #1e90ff;
  margin-top: 4rem;
  padding: 4rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 7.5rem;
  }
}

h2 {
  font-size: 1.625rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

strong {
  font-weight: 700;
}